  }

  .post-form {
    top: 100px;
    position: relative;

    width: 300px;
  }

  @media (max-width: 1000px) {
    .post-form {
      top: 10px;
    }
  }

  .PostFormDash {
    margin-top: 177px;
    position: relative;
    width: 300px;
  }
  .profiles {
    position: relative;
    margin-left: 21px;
    width: 100%;
  }
  .tablePosition {
    bottom: 300px;
    position: relative;
  }

  .margin-adjustpost {
    position: relative;
    margin-left: 10%;
  }
  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  /* Profiles Page */
  .profile {
    grid-template-columns: 1fr;
    text-align: center;
    position: relative;
  }

  .profile ul {
    display: none;
  }

  /* Profile Page */

  .profile-top img,
  .profile img {
    width: 200px;
  }

  .set-profiletopImg {
    margin-right: 0px;
  }

  .profile-grid {
    grid-template-areas:
      'top'
      'about'
      'exp'
      'edu'
      'github';
  }

  .profile-about .skills {
    flex-direction: column;
  }

  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem;
    padding: 40px 1.5rem !important;
  }

  .post {
    grid-template-columns: 1fr;
  }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }
}

